<template>
  <div class="exam">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-tabs v-model="table.query.recruitType" @tab-click="handleClick">
        <el-tab-pane label="文化招生" name="CULTURE"></el-tab-pane>
        <el-tab-pane label="特长招生" name="SPECIALITY"></el-tab-pane>
      </el-tabs>
      <div class="btn-block">
        <div
            @click="$router.push(`/recruit/exam/batch/0?recruitType=${table.query.recruitType}`)"
            class="btn-main-deepen">添加招生批次</div>
      </div>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="20%">考试批次</th>
          <th width="12%">考试时间</th>
          <th width="10%">考点数</th>
          <th width="10%">考点最多容纳<br>总人数</th>
          <th width="10%">报考人数</th>
          <th width="9%">实际考生数</th>
          <th width="9%">状态</th>
          <th width="20%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr :class="{'forbidden': item.batchStatus === 'FORBID'}" v-for="item in table.list">
          <td>{{item.title}}</td>
          <td>{{(item.examTime || '--') | substrTime }}</td>
          <td>{{item.countExamCentre || '--'}}</td>
          <td>{{item.maxStudentNum || '--'}}</td>
          <td>{{item.applyStudentNum || '--'}}</td>
          <td>{{item.realStudentNum || '--'}}</td>
          <td>{{$enums.BatchStatus.getName(item.batchStatus)}}</td>
          <td>
            <span v-show="item.batchStatus === 'OPEN'" @click="disableBatch(item)" class="oper">禁用</span>
            <span v-show="item.batchStatus === 'FORBID'" @click="openBatch(item)" class="oper">启用</span>
            <span @click="goToEdit(item)" class="oper">编辑</span>
            <span @click="goToSite(item)" class="oper">查看考点</span>
            <span @click="goToExaminee(item)" class="oper">查看考生</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "考试管理", path: ""}],
        table:{
          url: '/rbatch/queryBatchPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query:{
            recruitType: this.$route.query.recruitType || 'CULTURE'
          }
        }
      }
    },
    methods: {
      // 禁用考试批次
      async disableBatch(item){
        await this.$confirm(`您确认禁用【${item.title}】吗`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/rbatch/disableBatch.do',
          query:{
            id: item.id
          }
        })
        this.$remind('禁用成功')
        this.$search(this.table)
      },
      // 开启考试批次
      async openBatch(item){
        await this.$confirm(`您确认启用【${item.title}】吗`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/rbatch/enableBatch.do',
          query:{
            id: item.id
          }
        })
        this.$remind('启用成功')
        this.$search(this.table)
      },
      // 前往编辑界面
      goToEdit(item){
        this.$router.push({
          path: '/recruit/exam/batch/' + item.id,
          query:{
            recruitType: this.table.query.recruitType
          }

        })
      },
      // 前往查看考点界面
      goToSite(item){
        this.$router.push({
          path: '/recruit/exam/site',
          query:{
            id: item.id,
            batch: item.title,
            type: this.table.query.recruitType
          }
        })
      },
      // 前往查看考生
      goToExaminee(item){
        this.$router.push({
          path: '/recruit/exam/batch-examinee',
          query:{
            batch: item.title,
            id: item.id,
            type: this.table.query.recruitType
          }
        })
      },
      handleClick(){
        this.$search(this.table)
      }
    },
    mounted(){
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .exam
    .btns
      position: absolute
      right: 15px
      top: 6px
      z-index: 9
</style>
